import { RGBAColor } from '@/app/shared/client/type/component/product-list-component';
import { StoreImageInfo } from '@/app/website/client/type/store-image';

export function getStoreImageInfo(
  imageUrl: string | undefined,
  thumbnailImageUrl: string | undefined,
  dominatingColor: RGBAColor | undefined,
  homogeneity: boolean | undefined,
): StoreImageInfo {
  let imageBackgroundColor = {};
  if (dominatingColor) {
    const red = dominatingColor?.red;
    const green = dominatingColor?.green;
    const blue = dominatingColor?.blue;
    const alpha = dominatingColor?.alpha;
    imageBackgroundColor = { backgroundColor: `rgba(${red}, ${green}, ${blue}, ${alpha})` };
  }

  return {
    imageUrl,
    thumbnailImageUrl,
    imageBackgroundColor,
    homogeneity,
  };
}

export function getStoreImageUrl(
  imageInfo?: { imageUrl?: string, thumbnailImageUrl?: string },
): string | undefined {
  if (!imageInfo) {
    return undefined;
  }
  const { imageUrl } = imageInfo;

  if (imageUrl !== undefined) {
    return `url(${imageUrl})`;
  }

  return undefined;
}

export function getStoreThumbnailImageUrl(
  imageInfo?: { imageUrl?: string, thumbnailImageUrl?: string },
): string | undefined {
  if (!imageInfo) {
    return undefined;
  }
  const { thumbnailImageUrl, imageUrl } = imageInfo;

  if (thumbnailImageUrl !== undefined) {
    return `url(${thumbnailImageUrl})`;
  }

  if (imageUrl !== undefined) {
    return `url(${imageUrl})`;
  }

  return undefined;
}

export function getNoImageClass(id: number, imageUrl?: string): string {
  return imageUrl === undefined
    ? `ins-component__bg--color-${id % 10}`
    : '';
}
