













































































import {
  computed, defineComponent, PropType, ref,
} from '@vue/composition-api';
import {
  CategoryListComponentContent,
  CategoryListComponentDesign,
  CategoryListComponentCpDesign,
} from '@/app/shared/client/type/component/category-list-component';
import {
  getImageLayoutClass,
  getImageSizeClass,
} from '@/app/website/client/logic/store-design-settings';
import { calculateVisibilityOption } from '@/app/website/client/logic/cp-design-settings';
import { CategoryCardElementBehavior } from '@/app/website/client/type/cp-design-settings';
import {
  getStoreImageInfo,
  getStoreImageUrl,
  getNoImageClass, getStoreThumbnailImageUrl,
} from '@/app/website/client/logic/store-image';
import StoreImage from '@/app/website/client/view/component/StoreImage.vue';

interface CategoryListComponentProps {
  content: CategoryListComponentContent;
  design: CategoryListComponentDesign;
  cpDesign: CategoryListComponentCpDesign;
}

function calculateCategoryCardElementBehavior(
  hasCategoryImage: boolean,
  editorVisibilitySetting?: boolean,
  cpLayout?: string,
): CategoryCardElementBehavior {
  return {
    showOnImage: calculateVisibilityOption(editorVisibilitySetting,
      () => cpLayout === undefined
        || cpLayout === 'SHOW_ON_IMAGE'
        || cpLayout === 'HIDE'
        || cpLayout === 'SHOW_TEXT_ONLY'
        || (cpLayout === 'SHOW_ON_HOVER' && !hasCategoryImage)),
    showBelowImage: calculateVisibilityOption(editorVisibilitySetting,
      () => cpLayout === 'SHOW_BELOW_IMAGE'),
    showOnHover: calculateVisibilityOption(editorVisibilitySetting,
      () => cpLayout === 'SHOW_ON_HOVER' && hasCategoryImage),
  };
}

const CategoryListComponent = defineComponent({
  components: {
    StoreImage,
  },
  props: {
    content: {
      required: true,
      type: Object as PropType<CategoryListComponentContent>,
    },
    design: {
      required: true,
      type: Object as PropType<CategoryListComponentDesign>,
    },
    cpDesign: {
      required: true,
      type: Object as PropType<CategoryListComponentCpDesign>,
    },
  },
  setup(props: CategoryListComponentProps, { root }) {
    const wrapRef = ref<HTMLElement>();

    const categories = computed(() => props.content.categories.map((category) => {
      const imageUrl = getStoreImageUrl(category);
      const thumbnailImageUrl = getStoreThumbnailImageUrl(category);
      const noImageClass = getNoImageClass(category.id, imageUrl);

      const imageInfo = getStoreImageInfo(
        imageUrl,
        thumbnailImageUrl,
        category.imageBorderInfo?.dominatingColor,
        category.imageBorderInfo?.homogeneity,
      );

      return {
        id: category.id,
        name: category.name,
        url: category.url,
        imageInfo,
        noImageClass,
      };
    }));

    const categoryLinkTarget = root.$store.getters.isPreviewMode ? '_blank' : '_self';

    // дизайн настройки в CP
    const hasCategoryImage = computed(() => props.design.categoryImage?.visible || false);
    const imagePosition = computed(() => props.cpDesign.imagePosition);

    const categoryImageSizeClass = getImageSizeClass(props.cpDesign.imageSize);
    const categoryImageLayoutClass = getImageLayoutClass(props.cpDesign.imageLayout);
    const titleBehavior = computed(() => calculateCategoryCardElementBehavior(
      hasCategoryImage.value,
      props.design.categoryName?.visible,
      props.cpDesign.titleBehavior,
    ));

    const hasCategoryTitle = computed(() => props.design.categoryName?.visible);

    return {
      wrapRef,
      categories,
      hasCategoryTitle,
      hasCategoryImage,
      categoryLinkTarget,
      imagePosition,
      categoryImageSizeClass,
      categoryImageLayoutClass,
      titleBehavior,
    };
  },
});

export default CategoryListComponent;
