import { ref, Ref } from '@vue/composition-api';
import { debounce } from '@/app/shared/client/helper/hof';

interface TileOffsetSettings {
  tileOffsetTop: Ref<number>;
  tileOffsetHeight: Ref<number>;
  wrapOffsetWidth: Ref<number>;
  subscribeOnTileOffsetChange: () => void;
}

export function useTileOffsetSettings(
  tileRef: Ref<HTMLElement | null | undefined> | undefined,
  wrapRef?: Ref<HTMLElement | null | undefined> | undefined,
): TileOffsetSettings {
  const tileOffsetTop = ref(0);
  const tileOffsetHeight = ref(0);
  const wrapOffsetWidth = ref(0);

  function updateOffset(): void {
    if (tileRef?.value !== undefined && tileRef.value !== null) {
      tileOffsetTop.value = tileRef.value.offsetTop;
      tileOffsetHeight.value = tileRef.value.offsetHeight;
    }

    if (wrapRef?.value !== undefined && wrapRef.value !== null) {
      wrapOffsetWidth.value = wrapRef.value.offsetWidth;
    }
  }

  function subscribeOnTileOffsetChange(): void {
    const debounceUpdateOffset = debounce(updateOffset, 120); // позже, чем updateViewportDimensions
    window.addEventListener('resize', debounceUpdateOffset);
    updateOffset();
  }

  return {
    tileOffsetTop,
    tileOffsetHeight,
    wrapOffsetWidth,
    subscribeOnTileOffsetChange,
  };
}

export default {};
