

















import { computed, defineComponent, PropType } from '@vue/composition-api';
import { isCoverImagePosition } from '@/app/website/client/logic/cp-design-settings';
import { StoreImageInfo } from '@/app/website/client/type/store-image';

const StoreImage = defineComponent({
  props: {
    imageInfo: {
      required: true,
      type: Object as PropType<StoreImageInfo>,
    },
    primary: {
      required: false,
      type: Boolean,
      default: false,
    },
    secondary: {
      required: false,
      type: Boolean,
      default: false,
    },
    imagePosition: {
      required: false,
      type: String,
      default: undefined,
    },
  },
  setup(props) {
    const cover = computed(
      () => isCoverImagePosition(props.imageInfo.homogeneity, props.imagePosition),
    );
    return {
      cover,
    };
  },
});
export default StoreImage;
