export function getImageSizeClass(imageSize: string | undefined): string {
  if (imageSize === undefined) {
    return 'ins-component--medium';
  }
  return `ins-component--${imageSize.toLowerCase()}`;
}

export function getProductInfoLayoutClass(layout: string | undefined): string {
  if (layout === undefined) {
    return 'ins-component--layout-center';
  }
  return `ins-component--layout-${layout.toLowerCase()}`;
}

export function getImageLayoutClass(imageLayout: string | undefined): string {
  switch (imageLayout) {
    case 'PORTRAIT_0667':
      return 'ins-component--portrait2';
    case 'PORTRAIT_075':
      return 'ins-component--portrait1';
    case 'LANDSCAPE_1333':
      return 'ins-component--landscape1';
    case 'LANDSCAPE_15':
      return 'ins-component--landscape2';
    case 'SQUARE_1':
    default:
      return 'ins-component--square';
  }
}
