import { Store } from 'vuex';
import { nameOf } from '@/app/shared/client/helper/base';

interface HasTranslations {
  translations: Record<string, string>;
}

interface HasTranslationsInContext {
  context: HasTranslations;
}

function hasTranslations(
  state: HasTranslations | HasTranslationsInContext,
): state is HasTranslations {
  return nameOf<HasTranslations>('translations') in state;
}

export type I18nFunction = (
  label: string,
  replaces?: Record<string, string>,
) => string;

export default function useI18N(
  store: Store<HasTranslations | HasTranslationsInContext>,
): I18nFunction {
  function i18n(label: string, replaces: Record<string, string> | undefined = undefined): string {
    let translations;
    if (hasTranslations(store.state)) {
      translations = store.state.translations;
    } else {
      translations = store.state.context.translations;
    }

    let translation = translations[label];
    if (translation === undefined) {
      return `!${label}!`;
    }

    if (replaces !== undefined) {
      Object.keys(replaces).forEach((key) => {
        const reg = new RegExp(key, 'g');
        translation = translation.replace(reg, replaces[key]);
      });
    }

    return translation;
  }

  return i18n;
}
