import { ref, Ref } from '@vue/composition-api';

interface WindowScrollSettings {
  windowScrollTop: Ref<number>;
  subscribeOnWindowScrollChange: () => void;
}

export function useWindowScrollSettings(): WindowScrollSettings {
  const windowScrollTop = ref(0);

  function updateWindowScrollTop(): void {
    if (typeof document === 'undefined') {
      return;
    }

    windowScrollTop.value = document.documentElement.scrollTop || document.body.scrollTop;
  }

  function subscribeOnWindowScrollChange(): void {
    window.addEventListener('scroll', updateWindowScrollTop);
    updateWindowScrollTop();
  }

  return {
    windowScrollTop,
    subscribeOnWindowScrollChange,
  };
}

export default {};
