export function calculateVisibilityOption(
  editorVisibilitySetting: boolean | undefined,
  visibilityPredicate: () => boolean,
): boolean {
  if (editorVisibilitySetting !== undefined && !editorVisibilitySetting) {
    // настройки IS эдитора в приоритете
    return false;
  }
  return visibilityPredicate();
}

export function isCoverImagePosition(
  homogeneity?: boolean,
  cpImagePosition?: string,
): boolean {
  if (cpImagePosition === undefined || cpImagePosition === 'AUTO') {
    // в AUTO режиме позиционирование картинки определяется в зависимости от её homogeneity
    return !homogeneity;
  }
  return cpImagePosition === 'COVER';
}

export default {};
