import {
  BackgroundType,
  GeneralDesignSettings,
} from '@/app/shared/client/type/settings/general-settings';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { Color } from '@/app/shared/client/type/color';

function isColorSimilar(
  globalColor?: Color,
  tileColor?: Color,
): boolean {
  if (tileColor?.hsl === undefined) {
    return true;
  }

  const globalColorH = (globalColor?.hsl?.h ?? 0).toFixed(3);
  const globalColorS = (globalColor?.hsl?.s ?? 0).toFixed(3);
  const globalColorL = (globalColor?.hsl?.l ?? 1).toFixed(3);

  const tileColorH = tileColor?.hsl?.h.toFixed(3);
  const tileColorS = tileColor?.hsl?.s.toFixed(3);
  const tileColorL = tileColor?.hsl?.l.toFixed(3);

  return globalColorH === tileColorH
    && globalColorS === tileColorS
    && globalColorL === tileColorL;
}

function isColorTransparent(
  color?: Color,
): boolean {
  return color?.hsl?.a === 0;
}

export function checkIfTileTransparent(
  tileSettings?: GeneralDesignSettings,
  siteSettings?: GlobalDesign,
): boolean {
  const tileBackground = tileSettings?.background;

  switch (tileBackground?.type) {
    case BackgroundType.GRADIENT:
      return (isColorTransparent(tileBackground?.gradient?.fromColor)
          || isColorSimilar(siteSettings?.color?.background, tileBackground?.gradient?.fromColor))
        && (isColorTransparent(tileBackground?.gradient?.toColor)
          || isColorSimilar(siteSettings?.color?.background, tileBackground?.gradient?.toColor));
    case BackgroundType.SOLID:
      return isColorTransparent(tileBackground?.solid?.color)
        || isColorSimilar(siteSettings?.color?.background, tileBackground?.solid?.color);
    default:
      return false;
  }
}

export default {};
